@mixin reboto-light {
    font-weight: 300;
    font-size: 15px; }

* {
    margin: 0;
    font-family: Roboto, sans-serif; }

nav {
    height: 51px;
    background: #212121;
    @include reboto-light; }
.menu {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 14px 71px;
    .menu_link {
        color: #FFFFFF;
        text-decoration: none; } }
